.card-parent{
    display:flex;
    justify-content: center;
    text-align: center;
    width: auto;
    border-radius: 15px;
    border: 1px solid black;
    border-style: none;
    margin-bottom: 1rem;
    overflow: hidden;
    cursor: pointer;
    
    
    

    
}

.main-card--parent{
    width: 100%;
}

#current-project  {
    font-size: 0.7rem;
    margin: 3.0rem;
}

.card-parent :hover{
    opacity: 0.6;
    box-shadow: inset;
    
}

.card-parent a  {
    text-decoration: none;
    
}

.card-parent h3  {
    font-size: 1rem;
    color:white;
    
}

.app-screen--shot{

 width:100%;
 border-radius: 0px;
 cursor: pointer;
 
}
.app-screen--shot img{
    
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
   
    
   }

