.animate__animated.animate__fadeIn {
    --animate-duration: 3s;
}


.NavBarItems{
    display: flex;
    position: sticky;
    top: 0;
    align-items: center;
    background: white;
    height: 60px;
    font-size: 0.7rem;
    padding: 0 10rem 0 10rem;
    z-index: 4;

}

.navbar-logo{
    color: black;
    justify-self: start;
    cursor: pointer;
  
}

.nav-menu {
    display: flex;
    width: 100%;
    justify-content: flex-end;
    list-style: none;
   

}

.nav-links{
    display: flex;
    justify-content: center;
    color: black;
    text-transform: uppercase;
    font-weight: bold;
    text-decoration: none;
    margin: 0.5rem 0 0.5rem 1rem;
    opacity: 0.4;
}



.nav-links:hover{
    

 
    opacity: 1;
    transition: all 0.2s ease-out;
}

.nav-icons{
    font-size: medium;
}

.fa-bars{
    color: #ffffff;
}

.nav-links-mobile {
    display: none;
}

.menu-icon{
    display: none;
}

@media (max-width:830px){
   .NavBarItems{
    display: flex;
    padding: 0.5rem 2rem 0 2rem;
    margin: 0 0 0 0;

   }

   .navbar-logo{
       display: flex;
       justify-content: center;
       text-align: center;

   }

   .nav-menu {
       display: flex;
       justify-content: flex-end;
        margin-left: 1rem;
   }

   
}
