*, body, html{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: Helvetica Neue, Arial, sans-serif;
}

.app-content{
    padding: 0 2rem 0 2rem;
    height: 100%;
    overflow: hidden;
    
}


.app-project-container{
    
   
    justify-content: center;
    margin: 4rem 0 4rem 0;


 

}


.typing-effect-container{
    position: relative;
    height: 100%;
    width: 100%;
}

.banner-image--parent{
    display:flex;
    justify-content: center;
    width:100%;
   
    
}


.banner-image--skeleton--right{
    position: absolute;
    max-width: 450px;
    width:100%;
    bottom: 10%;
    left:50%


}

.banner-image--skeleton--left{
    position: absolute;
    max-width: 450px;
    width:100%;
    bottom: 10%;
    right:50%


}