.main-container-avatar{
    width: 100%;
    text-align : center;
    margin: 0 auto 0 auto;
    overflow:hidden;

  
}

.hiding-parent{
    position: relative;
    overflow: hidden;
}


.transparent-container{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 2rem;
    height: 100%;
    text-decoration: solid black;
    color:#282828;
    text-transform: uppercase;
    z-index: 3;
    opacity: 0.5;

   

}

.transparent-container h1{
   font-size: 8rem;

}



